  src: local("Montserrat"), url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  overflow-x: hidden;
  margin-right: calc(-1 * (100vw - 100%));
}

body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #333333;
}

img {
  max-width: 100%;
  object-fit: contain;
}

.container {
  min-width: 320px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}
@media (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding: 0 40px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 100%;
  }
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  grid-template-columns: 100%;
}

.page__body a {
  text-decoration: none;
}

.page__body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (min-width: 768px) {
  .logo__img {
    width: 82px;
  }
}
@media (min-width: 1440px) {
  .logo__img {
    width: 170px;
  }
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-wrapper {
  height: 100%;
}

.swiper-button-prev,
.swiper-button-next {
  top: 22px;
  height: 100%;
}
@media (min-width: 768px) {
  .swiper-button-prev,
.swiper-button-next {
    width: 50px;
  }
}

.swiper-button-prev {
  left: 0;
}

.swiper-button-next {
  right: 0;
}

.swiper-button-prev::before,
.swiper-button-prev::after,
.swiper-button-next::before,
.swiper-button-next::after {
  content: "";
  position: absolute;
  width: 14px;
  height: 2px;
  background-color: #7859cf;
}

.swiper-button-prev::before {
  left: 3px;
  top: 321px;
  transform: rotate(-46deg);
}
@media (min-width: 768px) {
  .swiper-button-prev::before {
    left: 33px;
    top: 315px;
  }
}
@media (min-width: 1440px) {
  .swiper-button-prev::before {
    left: 33px;
    top: 352px;
  }
}

.swiper-button-prev::after {
  left: 3px;
  top: 331px;
  transform: rotate(46deg);
}
@media (min-width: 768px) {
  .swiper-button-prev::after {
    left: 33px;
    top: 325px;
  }
}
@media (min-width: 1440px) {
  .swiper-button-prev::after {
    left: 33px;
    top: 362px;
  }
}

.swiper-button-prev:active {
  background: linear-gradient(90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
}

.swiper-button-next::before {
  left: 10px;
  top: 321px;
  transform: rotate(46deg);
}
@media (min-width: 768px) {
  .swiper-button-next::before {
    left: 3px;
    top: 315px;
  }
}
@media (min-width: 1440px) {
  .swiper-button-next::before {
    top: 352px;
  }
}

.swiper-button-next::after {
  left: 10px;
  top: 331px;
  transform: rotate(-46deg);
}
@media (min-width: 768px) {
  .swiper-button-next::after {
    left: 3px;
    top: 325px;
  }
}
@media (min-width: 1440px) {
  .swiper-button-next::after {
    top: 362px;
  }
}

.swiper-button-next:active {
  background: linear-gradient(90deg, rgba(120, 89, 207, 0) 0%, rgba(120, 89, 207, 0.25) 100%);
}

.swiper-pagination {
  display: none;
}
@media (min-width: 1440px) {
  .swiper-pagination {
    display: inline-block;
  }
}

.swiper-button-prev.swiper-button-disabled {
  opacity: 1;
}

.swiper-button-prev.swiper-button-disabled::before,
.swiper-button-prev.swiper-button-disabled::after {
  background-color: #bdbdbd;
}

.swiper-pagination-bullets.swiper-pagination-horizontal {
  bottom: 117px;
}

.swiper-pagination-bullet {
  width: 60px;
  height: 4px;
  border-radius: 0;
  background-color: #ffffff;
  opacity: 1;
}
.swiper-pagination-bullet:hover {
  background-color: rgba(120, 89, 207, 0.25);
}

.swiper-pagination-bullet-active {
  background-color: #7859cf;
}

.swiper-slide {
  height: auto;
}
@media (min-width: 768px) {
  .swiper-slide {
    padding-bottom: 199px;
  }
}
@media (min-width: 1440px) {
  .swiper-slide {
    padding-bottom: 50px;
  }
}

.burger {
  position: absolute;
  display: none;
  padding: 0;
  width: 40px;
  height: 60px;
  border: none;
  background-color: transparent;
  overflow: hidden;
  cursor: pointer;
}
.burger--visible {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.burger:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .burger {
    display: none;
  }
}

.burger__line {
  display: block;
  width: 16px;
  height: 1.5px;
  background-color: #333333;
  transition: all 0.3s ease-out;
}
.burger__line:not(:last-child) {
  margin-bottom: 4.5px;
}

.burger.burger--open .burger__line:nth-child(2) {
  transform: translateX(40px);
  transition: all 0.2s ease-out;
}

.burger.burger--open .burger__line:nth-child(1) {
  transform: translateY(5px) rotate(45deg);
  transition: all 0.2s ease-out;
  transition-delay: 0.2s;
}

.burger.burger--open .burger__line:nth-child(3) {
  transform: translateY(-7px) rotate(-225deg);
  transition: all 0.2s ease-out;
  transition-delay: 0.2s;
}

.button {
  display: inline-block;
  padding: 11px 20px;
  font-size: 16px;
  font-weight: 400;
  color: #ffffff;
  background-color: #7859cf;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.button--checkout {
  position: relative;
  font-size: 14px;
  padding: 10px 20px 10px 46px;
}
.button--checkout::before {
  position: absolute;
  content: "";
  top: 10px;
  left: 20px;
  width: 20px;
  height: 20px;
  background-image: url("img/icons/icon-checkout.svg");
  background-repeat: no-repeat;
}
